body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color:#EFF1F7;
  color:#373D50 ;
 
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/*añadidos*/
.todo-element{
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.congrats-msg{
  display: none;  
  font-weight: bold;
}
.error-input{
  display: none;
  color:red;
}
.button-styles{
  margin-top: 100px; 
  margin-bottom: 20px;
  color:red;
}

.header-styles{
  width: 100vw;
  height: 50vh;
  background-image: url(/todo-list/static/media/banner.a1164054.jpg);;
  background-size: cover;
  background-repeat: no-repeat;
  background-position-y: -200px;
}
.enter-tasks-block{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.info{
  color: #3A4976;
  font-weight: bold;
 }
 .footer-styles{
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: center;
   background-color:darkslategrey;
   color:white;
   font-weight: bold;
   font-size: large;
   height: 20vh;
   width: 100vw;
 }
 .button-print{
   border-radius: 700px;
 }
 .buttons-group{
   display: flex;
   flex-direction: row;
   align-items: center;
  justify-content:center;
  margin-top: 20px;
  margin-bottom: 20px;
 }

@media only screen and (max-width: 768px) {
  .header-styles{
    width: 100vw;
    height: 50vh;
    background-image: url(/todo-list/static/media/banner.a1164054.jpg);;
    background-size: cover;
    background-repeat: no-repeat;
    background-position-y: 0px !important;
    background-position-x: -50px
    
  }
  .enter-tasks-block{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .info{
   
    padding:10px;
  }
  
  img{
    width:auto;
    height: 50vh;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

